*{
  box-sizing: border-box;
}
body{
  margin: 0;
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.container{
  background-color: blue;
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.card{
  text-align: center;
  background-color: aliceblue;
  width: 50%;
  height: 50%;
  padding: 10px;
  border-radius: 10px;
  text-align: center;
}

.box_card{
  width: 100%;
  min-height: 150px;
  overflow: auto;
}

.box{
  display: flex;
  border: 1px solid grey;
  width: 100%;
  
  justify-content: space-evenly;
  align-items: center;
  margin: 10px 0;
}
.mx{
  min-height: 150px;
}